import "../sass/academy.scss"
import React from "react"

//to make graphql queries
import { graphql } from "gatsby"

export default function AcademyPage({ data }) {

  console.log(data);
  return (
    
    <div id="academy"> 
      <h1>ACADEMY PAGE</h1>
      <p>{data.wpPage.acfAcademy.descripcion}</p>
    </div>            
         
  )

}

